.row{
    margin-left: 5px;
    width: 100vw;
}
.row_posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row_posters::-webkit-scrollbar{
    display: none;
}
.row>h2{
    margin-left: 10px;
    color: white;
}


.row_poster{
    object-fit: contain;
    width: 100%;
    max-height: 100px;
    margin-left: 10px;
    transition: transform 450ms;
}
.row_poster:hover{
    transform: scale(1.08);
    opacity: 1;
}

.row_posterLarge{
    max-height: 250px;
}

.row_posterLarge:hover{
    transform: scale(1.09);
    opacity: 1;
}